<template>
  <div style="padding: 10px;">
    <Row>
      <Col span="8" offset="6">
      <Form ref="formEditUser" :model="formEditUser" :rules="ruleEditUser" :label-width="80">
        <Form-item label="头像" style="margin-bottom: 10px;">
          <div class="ivu-upload"
               style="width: 100px; height: 100px; margin-bottom: 10px; display: inline-block; position: relative;border-radius: 50px; overflow: hidden;">
            <input type="file" @change="changeImg" class="ivu-upload-input-b">
            <img v-if="formEditUser.imgUrl" style="width: 250px; height: 100px;" :src="baseUrl + formEditUser.imgUrl">
            <div v-if="!formEditUser.imgUrl" class="ivu-upload ivu-upload-drag"
                 style="height: 100%; border-radius: 50px;">
              <div style="padding: 10px 0px;">
                <i class="ivu-icon ivu-icon-ios-cloud-upload"
                   style="font-size: 40px; color: rgb(51, 153, 255);"></i>
                <p>上传头像</p>
              </div>
            </div>
          </div>
          <Button type="ghost" size="small" style="float: right;" @click="editPassword = true">修改密码</Button>
        </Form-item>
        <Form-item label="用户名">
          <Input type="text" value="Dared" readonly>
          </Input>
        </Form-item>
        <Form-item prop="nickName" label="昵称">
          <Input type="text" v-model="formEditUser.nickName" placeholder="昵称">
          </Input>
        </Form-item>
        <Form-item prop="email" label="邮箱">
          <Input type="text" v-model="formEditUser.email" placeholder="邮箱">
          </Input>
        </Form-item>
        <Form-item>
          <Button type="primary" :loading="editUserLoading"
                  @click="handleSubmit('formEditUser', formEditUser)">
            确认
          </Button>
        </Form-item>
      </Form>
      </Col>
    </Row>
    <Modal
      title="修改密码"
      @on-ok="subEditPassword"
      @on-cancel="clearForm"
      v-model="editPassword"
      :loading="passwordLoading"
      class-name="vertical-center-modal">
      <Form ref="formPassword" :model="formPassword" :rules="rulePassword" :label-width="80">
        <Form-item prop="opassword" label="密码">
          <Input type="password" v-model="formPassword.opassword">
          </Input>
        </Form-item>
        <Form-item prop="password" label="新密码">
          <Input type="password" v-model="formPassword.password">
          </Input>
        </Form-item>
        <Form-item prop="passwordCheck" label="确认密码">
          <Input type="password" v-model="formPassword.passwordCheck">
          </Input>
        </Form-item>
      </Form>
    </Modal>
  </div>
</template>
<script>
  import getData from '../config/getData'
  import baseUrl from '../config/baseUrl'
  import { getUser } from '../config/data'
  export default {
    created () {
      getUser().then(user => {
        this.user = user
        this.formEditUser.imgUrl = user.imgUrl
        this.formEditUser.nickName = user.nickName
        this.formEditUser.email = user.email
      })
    },
    data () {
      return {
        baseUrl,
        editPassword: false,
        passwordLoading: true,
        user: null,
        editUserLoading: false,
        formEditUser: {
          nickName: '',
          imgUrl: '',
          email: ''
        },
        ruleEditUser: {
          nickName: [
            { required: true, message: '请填写昵称', trigger: 'blur' }
          ],
          email: [
            { required: false, message: '邮箱不能为空', trigger: 'blur' },
            { type: 'email', message: '邮箱格式不正确', trigger: 'blur' }
          ]
        },
        formPassword: {
          opassword: '',
          password: '',
          passwordCheck: ''
        },
        rulePassword: {
          opassword: [
            { required: true, message: '请填写密码', trigger: 'blur' }
          ],
          password: [
            { required: true, message: '请填写密码', trigger: 'blur' },
            { type: 'string', min: 6, message: '密码长度不能小于6位', trigger: 'blur' }
          ],
          passwordCheck: [
            {
              validator: (rule, value, callback) => {
                if (value === '') {
                  callback(new Error('请再次输入密码'))
                } else if (value !== this.formPassword.password) {
                  callback(new Error('两次输入密码不一致!'))
                } else {
                  callback()
                }
              },
              trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      changeImg (event) {
        let file = event.target.files[0]
        if (file) {
          let formData = new window.FormData()
          let type = file.name.split('.')
          type = type[type.length - 1]
          formData.append('image', file, file.name)
          formData.append('format', type)
          formData.append('bus', 1)
          getData('fileUpload', formData).then(res => {
            this.formEditUser.imgUrl = res.data.data.url
          })
        }
      },
      handleSubmit (name, data) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.editUserLoading = true
            getData('userUpdate', { ...data }).then(() => {
              this.$Message.success('修改成功')
              this.editUserLoading = false
            }, () => {
              this.$Message.error('修改失败')
            })
          }
        })
      },
      subEditPassword () {
        this.$refs.formPassword.validate((valid) => {
          if (valid) {
            getData('updatePassword', { ...this.formPassword }).then(() => {
              this.$Message.success('修改成功')
              this.passwordLoading = false
              this.editPassword = false
              setTimeout(() => {
                this.passwordLoading = true
              }, 10)
            }, () => {
              this.passwordLoading = false
              setTimeout(() => {
                this.passwordLoading = true
              }, 10)
              this.$Message.error('修改失败')
            })
          } else {
            this.passwordLoading = false
            setTimeout(() => {
              this.passwordLoading = true
            }, 10)
          }
        })
      },
      clearForm () {
        this.formPassword.opassword = ''
        this.formPassword.password = ''
        this.formPassword.passwordCheck = ''
      }
    }
  }
</script>



